import React from 'react'
import useWindowSize from '../../../hooks/window/useWindowSize';
import { Link } from '@mui/material';

const bgColor = {
  width: '100%',
  height: '100vh',
  backgroundImage:
    `url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/main/parents_main_color.png')`,
  backgroundPosition: 'center',
  backgroundSize: 'cover',
};

const ServiceIntroPage = () => {

  const { width } = useWindowSize();

  const src = {
    download: {
      play: 'https://play.google.com/store',
      app: 'https://www.apple.com/kr/app-store/',
    },
  };

  return (
    <>
      {/* TopMenu */}
      <div className="sticky top-0 z-50 bg-[#1c1c1c] border border-solid border-b-[2px] border-x-0 border-t-0 border-[#ffffff]">
        <div className="container max-md:w-full m-auto py-7 flex items-center max-md:px-[20px]">
          <div>
            <a href="/">
              <img
                src={'https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/logo/logo.png'}
                className="mr-12 max-md:w-[200px] cursor-pointer"
              ></img>
            </a>
          </div>
        </div>
      </div>
      {/* main */}
      <div style={bgColor}>
        <div
          className={
            width < 1536
              ? 'container max-md:w-full m-auto flex-none pt-10 text-center'
              : 'container max-md:w-full m-auto flex items-center pt-16'
          }
        >
          <div className={ width < 1536 ?
            "container max-md:w-full w-[600px] m-auto" :
            "container max-md:w-full w-[943px]" 
            }>
            <img
              src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/main/parents_main.png"
              alt="태권월드 앱 소개"
              className="w-full"
            />
          </div>
          <div>
            <div
              className={
                width < 768
                  ? 'text-[25px] text-[#ffffff] font-bold mb-5'
                  : 'text-[45px] text-[#ffffff] font-bold mb-5'
              }
            >
              모든 도장 관리를 손쉽게.
              <br />
              다양하게 - 간편하게!
            </div>
            <div className="mb-12">
              <a href="/">
                <img
                  src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/home_btn.png"
                  alt="태권월드 홈 바로가기"
                />
              </a>
            </div>
            <div
              className={
                width < 1536
                  ? 'container max-md:w-full flex-none px-[20px] mb-3'
                  : 'container max-md:w-full flex items-center mb-6'
              }
            >
              <div className={"text-[18px] font-medium text-[#ffffff] tracking-normal mr-2"}>
                앱스토어 다운로드
              </div>
              <div
                className={
                  width < 1536
                    ? 'none'
                    : 'w-[264px] h-[1px] bg-[#ffffff] mt-3'
                }
              ></div>
            </div>
            <div
              className={
                width < 1536
                  ? 'container max-md:w-full flex justify-center gap-[10px] px-4'
                  : 'container max-md:w-full flex gap-[23px]'
              }
            >
              <Link className="cursor-pointer" href={src.download.play} target="_blank">
                <img
                  src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/app/google.png"
                  alt="구글 스토어 태권월드"
                  className={width < 768 ? 'w-[120px]' : 'w-full'}
                />
              </Link>
              <Link className="cursor-pointer" href={src.download.app} target="_blank">
                <img
                  src="https://tkdw-img.s3.ap-northeast-2.amazonaws.com/homepage/app/apple.png"
                  alt="앱 스토어 태권월드"
                  className={width < 768 ? 'w-[120px]' : 'w-full'}
                />
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ServiceIntroPage